body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;scrollbar-color:rgba(255,255,255,0.3) transparent;scrollbar-width:auto}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}::-webkit-scrollbar{width:16px}::-webkit-scrollbar-thumb{background-color:rgba(255,255,255,0.3);-webkit-transition:background-color 500ms linear;transition:background-color 500ms linear}::-webkit-scrollbar-thumb:hover{background-color:rgba(255,255,255,0.4)}
